<template>
  <div class="container">
    <div class="col-md-12">
      <h1>Boun - CmpE Slack Katılma İsteği</h1>
      <p>
        Bölüm öğrencilerinin dersleri tartışıp muhabbet edebileceği, teknoloji
        konuşup oyun arkadaşı bulabileceği bir slack grubu.
      </p>
    </div>
    <div class="col-md-3">
      <div class="input-container" style="margin-top: 0;">
        <label for="name">Ad</label><br  />
        <input type="text" name="name" v-model="firstName"/>
      </div>
    </div>
    <div class="col-md-3">
      <div class="input-container" style="margin-top: 0;">
        <label for="surname">Soyad</label><br  />
        <input type="text" name="surname" v-model="lastName" />
      </div>
    </div>
    <div class="clearfix"></div>
    <div class="col-md-6">
      <div class="input-container">
        <label for="email">Email</label><br  />
        <input type="email" name="email" v-model="email" />
      </div>
    </div>
    <div class="clearfix">
    </div>
    <div class="col-md-3">
      <div class="input-container">
        <label for="years">Giriş yılınız</label><br  />
        <select name="years" v-model="entrance">
          <option v-for="year in years">{{ year }}</option>
        </select>
      </div>
    </div>
    <div class="clearfix"></div>
    <div class="col-md-6">
      <div class="input-container">
        <input type="radio" name="who" value="yes" v-model="isCmpe"/>
        <label> Bilgisayar Mühendisliği Öğrencisiyim</label>
      </div>
    </div>
    <div class="clearfix"></div>
    <div class="col-md-3 error__container" v-if="error">
      <p>
        Hata! {{error}}
      </p>
    </div>
    <div class="clearfix">
    </div>
    <div class="col-md-3">
      <button v-on:click="postRequest">Davet İsteği Gönder</button>
    </div>
  </div>
</template>

<script>


export default {
  name: 'MakeRequest',
  data() {
    return {
      firstName: '',
      lastName: '',
      email: '',
      isCmpe: 'no',
      entrance: 2016,
      error: '',
      years: this.getYears(),
    };
  },

  methods: {
    getYears: function getYears() {
      const years = [];
      const startYear = new Date().getFullYear() - 1;
      const endYear = 1990;
      for (let i = startYear; i > endYear; i -= 1) {
        years.push(i);
      }
      return years;
    },

    validateInputs: function validateInputs() {
      this.$set(this, 'error', '');
      if (this.firstName === '') {
        this.$set(this, 'error', 'Ad boş bırakılamaz.');
        return false;
      }

      if (this.lastName === '') {
        this.$set(this, 'error', 'Soyad boş bırakılamaz.');
        return false;
      }

      if (this.email === '') {
        this.$set(this, 'error', 'Email boş bırakılamaz');
        return false;
      }

      if (this.isCmpe === 'no') {
        this.$set(this, 'error', 'Bu slack grubuna katılmak için CMPE olmalısınız.');
        return false;
      }

      return true;
    },

    postRequest: function postRequest() {
      this.validateInputs();
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  color: #727272;
}

.input-container{
  margin-top: 20px;
}

.error__container{
  margin-top: 15px;
}

.error__container p{
  font-size: 16px;
  color: red;
}


input[type='text'], input[type='email'] {
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 40px;
  padding: 0 10px;
  margin-top: 5px;
}

input[type='text']{
  text-transform: capitalize;
}

input[type='radio']{
  margin-right: 10px;
}

select{
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 40px;
  padding: 0 10px;
  margin-top: 5px;
  background: #fff;
}

label{
  font-size: 16px;
  color: #000000;
  font-weight: normal;
}

button{
    margin-top: 10px;
    width: 100%;
    border: 0;
    border-radius: 4px;
    padding: 14px 32px;
    cursor: pointer;
    height: 53px;
    display: inline-block;
    font-size: 17px;
    font-weight: 700;
    background-color: #42c299;
    color: #fff!important;
    text-shadow: none;
    box-shadow: 0 3px 10px rgba(0,0,0,.2);
}

button:hover{
      background-color: #3dbd94;
}

h1, h2 {
  color: #E32072;
}

p {
  color: #000000;
  font-size: 18px;
}

a {
  color: #42b983;
}
</style>



